<template>
  <!-- TODO:Update component => components -->
  <form
    class="container-fluid pt-5 w-100 vh-100 d-flex aling-items-center justify-content-center align-items-center"
    :class="{ formDark: mode, formLight: !mode }"
    style="overflow: hidden; position: relative"
    @submit.prevent="saveEmailAndPasswordUser"
  >
    <BtnGroup
      class="position-absolute"
      style="top: 20px"
      v-if="form == 1"
      :checkedLabel="'login'"
      :labelTwo="'newUser'"
      @sendType="receiveType"
    />
    <!-- <div
      v-if="form == 1"
      class="btn-group position-absolute"
      style="top: 20px"
      role="group"
      aria-label="Basic radio toggle button group"
    >
      <input
        type="radio"
        class="btn-check"
        v-model="type"
        value="login"
        name="btnradio"
        id="btnradio1"
      
        checked
      />
      <label
        class="btn btn-outline-primary"
        style="font-size: 13px"
        for="btnradio1"
        >login</label
      >

      <input
        type="radio"
        class="btn-check"
        v-model="type"
        value="newUser"
        name="btnradio"
        id="btnradio2"
     
      />
      <label
        class="btn btn-outline-primary"
        style="font-size: 13px"
        for="btnradio2"
        >newUser</label
      >
    </div> -->
    <div
      :class="{ form1: form != 1 }"
      class="form m-auto p-4 rounded d-flex flex-column justify-content-center"
      style=""
    >
      <span
        v-if="!mode"
        style="
          font-size: 40px;
          position: absolute;
          top: 0%;
          width: 100%;
          text-align: center;
          transform: translateX(-23px);
        "
        ><i class="fa-solid fa-crow fa-flip"></i
      ></span>
      <span class="w-100 text-center mb-2" style="font-size: 50px"
        ><i
          class="fa-solid fa-user-astronaut fa-shake"
          style="color: #517bc2"
        ></i
      ></span>
      <div class="form-floating mb-3">
        <input
          type="email"
          class="form-control"
          id="floatingInput"
          placeholder="name@example.com"
          v-model="email"
          :class="{ error: !errorPassword && massage }"
          required
        />
        <label for="floatingInput">Email address</label>
      </div>
      <div class="form-floating">
        <input
          type="password"
          class="form-control"
          id="floatingPassword"
          v-model="password"
          placeholder="password"
          required
          :class="{ error: errorPassword }"
        />
        <label for="floatingPassword">Password</label>
      </div>
      <button
        class="btn text-white px-3 mt-3"
        style="background: rgba(185, 13, 253, 0.67)"
      >
        Next
      </button>
    </div>
    <div
      :class="{ form1: form != 2 }"
      class="form m-auto p-4 rounded d-flex flex-column justify-content-center"
    >
      <div class="mb-3">
        <label for="formGroupExampleInput" class="form-label">First name</label>
        <input
          type="text"
          class="form-control"
          id="formGroupExampleInput"
          placeholder="First name"
          v-model="fName"
        />
      </div>
      <div class="mb-3">
        <label for="formGroupExampleInput2" class="form-label">Last name</label>
        <input
          type="text"
          class="form-control"
          id="formGroupExampleInput2"
          placeholder="Last name"
          v-model="lName"
        />
      </div>
      <input
        type="file"
        class="form-control"
        id="validationCustom05"
        accept="image/jpeg"
        @change="previewFiles"
        placeholder="your picture"
      />
      <button
        class="btn text-white px-3 mt-3"
        style="background: rgba(185, 13, 253, 0.67)"
      >
        Next
      </button>
    </div>
    <p
      v-if="massage"
      class="p-3 rounded"
      style="
        position: absolute;
        top: 20%;
        color: rgba(254, 60, 60, 0.855);
        background-color: rgb(255, 162, 162);
      "
    >
      {{ massage
      }}<i
        class="fa-solid fa-triangle-exclamation ms-2"
        style="color: #ff5252"
      ></i>
    </p>

    <div :class="{ form1: form != 3 }">
      <span style="font-size: 100px"
        ><i class="fa-regular fa-circle-check" style="color: #045367"></i
      ></span>
    </div>
    <div style="position: absolute; top: 85%; color: wheat">
      <span
        @click="form = 1"
        v-if="form == 1 || form == 2 || form == 3"
        class="p-3 rounded mx-2"
        style="background-color: rgba(38, 62, 147, 0.44); cursor: pointer"
        ><i class="fa-solid fa-1"></i></span
      >---
      <span
        @click="form = 2"
        v-if="form == 2 || form == 3"
        class="p-3 rounded mx-2"
        style="background-color: rgba(38, 62, 147, 0.723); cursor: pointer"
        ><i class="fa-solid fa-2"></i></span
      >---
      <span
        v-if="form == 3"
        class="p-3 rounded mx-2"
        style="background-color: rgb(38, 62, 147)"
        ><i class="fa-solid fa-3"></i
      ></span>
    </div>
    <!-- ANIMATION -->
    <span
      v-if="mode && form == 1"
      class="spider position-absolute d-flex justify-content-center"
      style="
        background-color: rgb(221, 221, 221);
        height: 207px;
        top: 60px;
        width: 2px;
      "
      ><i class="fa-solid fa-spider" style="color: #470000; font-size: 35px"></i
    ></span>
    <div v-if="!mode">
      <span class="bird1 brids position-absolute" style="font-size: 30px"
        ><i class="fa-solid fa-dove fa-shake"></i
      ></span>
      <span class="bird2 brids position-absolute" style="font-size: 30px"
        ><i class="fa-solid fa-dove fa-shake"></i
      ></span>
      <span class="bird3 brids position-absolute" style="font-size: 30px"
        ><i class="fa-solid fa-dove fa-shake"></i
      ></span>
      <span class="bird4 brids position-absolute" style="font-size: 30px"
        ><i class="fa-solid fa-dove fa-shake"></i
      ></span>
    </div>
  </form>
</template>

<script>
import BtnGroup from "./BtnGroup.vue";
import api from "@/api/api";
export default {
  name: "loginMain",
  data() {
    return {
      email: "",
      password: "",
      fName: "",
      lName: "",
      form: 1,
      type: "",
      massage: "",
    };
  },
  methods: {
    receiveType(type) {
      this.type = type;
    },
    saveEmailAndPasswordUser() {
      if (this.type == "newUser") {
        this.newUser();
      } else {
        this.checkUser();
      }
    },
    newUser() {
      this.massage = "";
      let check = this.users.findIndex((user) => user.email == this.email);
      if (check == -1) {
        if (this.form == 1 && this.email && this.password) {
          this.form += 1;
        } else if (this.form == 2 && this.fName && this.lName) {
          this.user.email = this.email;
          this.user.password = this.password;
          this.user.name = `${this.fName.trim()}-${this.lName.trim()}`;
          this.users.push(this.user);
          this.form += 1;
        }
      } else {
        this.massage =
          "The email already exists. It is not possible to register another account";
      }
    },
    checkUser() {
      // this.massage = "";
      // const authenticated = this.users.findIndex(
      //   (user) => user.email == this.email.trim() && user.password == this.password
      // );

      // if (authenticated > -1) {
      //   this.$store.state.indexLoginUser = authenticated;
      //   this.form = 3;
      //   return;
      // }

      // this.message = 'Username/password does not incorrect';
      for (let i = 0; i < this.users.length; i++) {
        if (
          this.users[i].email == this.email.trim() &&
          this.users[i].password == this.password
        ) {
          this.massage = "";
          this.$store.state.indexLoginUser = i;
          this.form = 3;
          return;
        } else if (
          this.users[i].email == this.email.trim() &&
          this.users[i].password != this.password
        ) {
          this.massage = "The password is incorrect";
          return;
        } else if (
          this.users[i].email != this.email.trim() &&
          this.users[i].password == this.password
        ) {
          console.log(`${this.password} // ${this.users[i].password}`);
          this.massage = "Email not found";
          return;
        } else if (
          this.users[i].email != this.email.trim() &&
          this.users[i].password != this.password
        )
          this.massage = "not found";
    }},
    previewFiles(e) {
      const image = e.target.files[0];
      const reader = new FileReader();
      if (image) {
        reader.readAsDataURL(image);
        reader.onload = (e) => {
          this.user.imgUser = e.target.result;
        };
      }
    },
    async apidata() {
      const list = await api.getListImages({ trend: "iphone", page: 1 });
    },
  },
  computed: {
    user() {
      return this.$store.state.user;
    },
    users() {
      return this.$store.state.users;
    },
    errorPassword() {
      if (this.massage == "The password is incorrect") {
        return true;
      }
      return false;
    },
    mode() {
      if (this.$store.state.modeSite == "Dark mode") {
        return true;
      } else return false;
    },
  },
  watch: {
    form: {
      handler() {
        if (this.form == 3) {
          setTimeout(() => this.$router.push("/"), 1000);
        }
      },
    },
  },
  components: { BtnGroup },
  mounted() {
    this.apidata();
  },
};
</script>

<style scoped>
.formLight {
  background: url("../../assets/backg.jpg");
  background-size: cover;
}
.formDark {
  background: url("../../assets/backgDark.jpg");
  background-size: cover;
}
.form {
  width: 340px;
  height: 340px;
  background-color: #eeeeeed5;
  position: relative;
  transition: 0.9s;
}
.form1 {
  position: absolute;
  transform: translateX(-1200px);
}
.error,
.error:focus {
  box-shadow: 0 0 0 0.25rem rgba(253, 13, 13, 0.477);
}
.error::after {
  content: "The password is incorrect";
  position: absolute;
  width: 100%;
  height: 10px;
  font-size: 20px;
  bottom: 50%;
  z-index: 121;
  left: 0;
  color: red;
}
input:focus {
  box-shadow: 0 0 0 0.25rem rgba(185, 13, 253, 0.329);
}
.bird1 {
  animation-delay: 0.5s;
}
.bird2 {
  animation-delay: 1s;
}
.bird3 {
  animation-delay: 1.5s;
}
.bird4 {
  animation-delay: 2s;
}
.brids {
  top: 0%;
  left: 0%;
  animation-name: bird;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
.spider i {
  animation-name: spider;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
.spider {
  animation-name: spider2;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}
/*  */
@keyframes bird {
  0% {
    left: 0%;
    top: 0%;
    color: blueviolet;
  }
  10% {
    top: 10%;
    left: 10%;
    color: rgb(3, 61, 0);
  }
  20% {
    left: 20%;
    top: 0%;
    text-shadow: 0px 0px 5px rgb(197, 238, 65);
  }
  30% {
    top: 10%;
    left: 30%;
    color: rgb(13, 133, 163);
  }
  40% {
    left: 40%;
    top: 0%;
  }
  50% {
    left: 50%;
    top: 10%;
    text-shadow: 0px 0px 5px red;
    color: rgb(54, 6, 99);
  }
  60% {
    left: 60%;
    top: 0%;
  }
  70% {
    left: 70%;
    top: 30%;
    color: rgba(3, 69, 0, 0.766);
  }
  80% {
    left: 80%;
    top: 40%;
    text-shadow: 0px 0px 13px rgb(225, 0, 255);
  }
  100% {
    left: 90%;
    top: 90%;
  }
}
@keyframes spider {
  0% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(200px);
    text-shadow: 0px 0px 5px red;
  }
}
@keyframes spider2 {
  0% {
    height: 0px;
  }
  100% {
    height: 208px;
  }
}
</style>
