<template>
  <!-- TODO: Update !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! -->
  <!-- TODO: Update !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! -->
  <!-- TODO: Update !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!! -->
  <div
    class="btn-group p-1 align-items-center"
    role="group"
    aria-label="Basic radio toggle button group"
    style="background-color: fixed"
  >
    <span
      v-if="classIconCheckedLabel"
      :class="{ active: !mode, offActive: mode }"
    >
    </span>
    <input
      type="radio"
      class="btn-check"
      v-model="type"
      :value="checkedLabel"
      name="btnradio"
      :id="checkedLabel.trim()"
    />
    <!-- btn-outline-primary -->
    <label
      @click="clickActive = true"
      class=""
      :class="{
        'btn btn-outline-primary ': !classIconCheckedLabel,
        labelChecked: classIconCheckedLabel,
      }"
      style="font-size: 13px"
      :for="checkedLabel.trim()"
      >{{ checkedLabel
      }}<i v-if="classIconCheckedLabel" :class="classIconCheckedLabel"></i
    ></label>
    <input
      type="radio"
      class="btn-check"
      v-model="type"
      :value="labelTwo.trim()"
      name="btnradio"
      :id="labelTwo"
    />
    <label
      @click="clickActive = false"
      class=""
      :class="{
        'btn btn-outline-primary border': !classIconCheckedLabel,
        labelTwo: classIconCheckedLabel,
      }"
      style="font-size: 13px"
      :for="labelTwo.trim()"
      >{{ labelTwo }} <i v-if="classIconlabelTwo" :class="classIconlabelTwo"></i
    ></label>
  </div>
</template>

<script>
export default {
  name: "btnGroup",
  props: {
    checkedLabel: String,
    classIconCheckedLabel: String,
    labelTwo: String,
    classIconlabelTwo: String,
  },
  data() {
    return {
      type: "",
      clickActive: true,
    };
  },
  watch: {
    type: {
      handler() {
        this.$emit("sendType", this.type.trim());
      },
    },
  },
  computed: {
    mode() {
      if (this.$store.state.modeSite == "Dark mode") {
        return true;
      } else return false;
    },
  },
};
</script>
<style scoped>
.labelChecked,
.labelTwo {
  color: white;
  padding: 10px 5px;
  cursor: pointer;
  border-radius: 10px;
  z-index: 121;
  transition: 0.5s;
}
.labelChecked:hover,
.labelTwo:hover {
  color: #b1b0ff97;
}
span {
  transition: 0.5s;
  position: absolute;
  width: 90px;
  height: 35px;
  border-radius: 6px;
  background: rgb(4, 79, 149);
}
.active {
  right: 50%;
}
.offActive {
  right: 1%;
}
</style>
